[data-theme='1'] {
    --color: #000000;
    --background: #FFFFFF;
}

[data-theme='2'] {
    --color: #000000;
    --background: #DDEEFF;
}

[data-theme='3'] {
    --color: #FFFFFF;
    --background: #212A34;
}

html {
    background-color: var(--background);
    color: var(--color);
}

.select {
    height: 36px;
    background-color: var(--background);
    padding: 8px 6px 11px 10px;
    color: var(--color);
    border: 1px solid var(--color);

    font-family: Montserrat, sans-serif;
    font-size: 14px;
    font-weight: 400;
}
